import { Component, HostBinding, Input } from '@angular/core';
import { PositionsType } from './reducer';



export class DockComponent {
   position: PositionsType = 'right';
   size: number = 0.3;
   visible: boolean = true;

  get absoluteSize(){
    return `${100 * this.size}%`;
  }

  get restSize(){
    return `${100 - (100 * this.size)}%`;
  }

   get leftPosition(){
    return this.calculatePosition('left', 'right');
  }

   get rightPosition(){
    return this.calculatePosition('right', 'left');
  }

   get topPosition(){
    return this.calculatePosition('top', 'bottom');
  }

   get bottomPosition(){
    return this.calculatePosition('bottom', 'top');
  }

  calculatePosition(primary: PositionsType, secondary: PositionsType) {
    if (this.visible) {
      switch (this.position) {
        case secondary:
          return this.restSize;
        default:
          return '0%';
      }
    }
    else {
      switch (this.position) {
        case primary:
          return `-${this.absoluteSize}`;
        case secondary:
          return '100%';
        default:
          return '0%';
      }
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ngrx-dock',
  template: `
    <div class="dock">
      <div class="dock-content">
        <ng-content></ng-content>
      </div>
    </div>
  `,
  styles: [`
    :host {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transition: all 0.3s;
      z-index: 9999;
    }

    .dock {
      position: absolute;
      z-index: 1;
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
      background-color: white;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    .dock-content {
      width: 100%;
      height: 100%;
      overflow: auto;
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: ({type: any, decorators?: DecoratorInvocation[]}|null)[] = [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'position': [{ type: Input },],
'size': [{ type: Input },],
'visible': [{ type: Input },],
'leftPosition': [{ type: HostBinding, args: ['style.left', ] },],
'rightPosition': [{ type: HostBinding, args: ['style.right', ] },],
'topPosition': [{ type: HostBinding, args: ['style.top', ] },],
'bottomPosition': [{ type: HostBinding, args: ['style.bottom', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
